import { useState } from 'react';
import Buttons from '../../Components/PracticeButtons';
import Chips from '../../Components/PracticeChips';
import useToast from '../../Hooks/useToast';
import shuffle from '../../Utils/shuffle';
import ProgressProps from './ProgressProps';
import './Style.scss';

interface StoryProps extends ProgressProps {
  randomize?: undefined | (() => sentences),
  sentences: sentences
}

type sentences = {
  en: string;
  hi: string[];
}[];

export default function Story({ randomize = undefined, sentences, setProgress }: StoryProps) {

  const

  [questions, setQuestions] = useState([...sentences]),
  [answer, setAnswer] = useState(""),

  mapSelection = (sentences: sentences) => {

    let selection: any = {};
  
    sentences.map(sentence => {
      return sentence.hi.map(option => {
        return option.split(" ").map(word => {
          return selection[word] = null;
        });
      })
    });

    return selection;
  },

  [selection, setSelection] = useState(mapSelection(sentences)),

  toast = useToast(),
  
  handleChip = (word: string) => {
    setAnswer(answer + " " + word);
  },

  handleBack = () => {
    setAnswer(answer.substr(0, answer.lastIndexOf(" ")));
  },

  handleRestart = () => {
    setAnswer("");
    setQuestions([...sentences]);
    randomize && setSelection(mapSelection([...sentences]));
    setProgress(0);
  },

  handleTraining = () => {
    handleRestart();

    const questions = randomize ? randomize() : [...sentences];
    setQuestions(shuffle(questions));
    setSelection(mapSelection(questions));
  },

  handleSubmit = () => {
    if (questions[0].hi.includes(answer.trim())) {

      toast('sahee!');
      setAnswer("");

      if (questions.length > 1) {
        questions.shift();
        setProgress(1 - questions.length / sentences.length);
      }
      else {
        questions[0].en = "Well done!"
        setProgress(1);
      }
    }
    else {
      toast('galat!');
    }
  };

  return (
    <>
      <section className="accent-bottom panel">
        <h4 className="center" style={{ background: "#ee6e73" }}>{questions[0].en}</h4>
        <h4 className="center" id="StoryAnswer">{answer}</h4>
        <Chips
          dictionary={selection}
          handleClick={handleChip} />
      </section>
      <Buttons
        handleBack={handleBack}
        handleRestart={handleRestart}
        handleTraining={handleTraining}
        handleSubmit={handleSubmit} />
    </>
  );
};