interface ProgressProps {
  handleBack: () => void,
  handleRestart: () => void,
  handleTraining: () => void,
  handleSubmit: () => void
}

export default function Progress({handleBack, handleRestart, handleTraining, handleSubmit}: ProgressProps) {

  return (
    <div className="row">
      <button
        className="btn col l2 m3 s4 waves-effect waves-light"
        onClick={handleBack}>
          <i className="material-icons left">chevron_left</i>
          Back
      </button>
      <button
        className="btn col s1 offset-l3 offset-m2 offset-s1 waves-effect waves-light"
        onClick={handleRestart}>
          <i className="material-icons">replay</i>
      </button>
      <button
        className="btn col s1 waves-effect waves-light"
        onClick={handleTraining}
          style={{
          left: ".25rem",
          position: "relative"
        }}>
          <i className="material-icons">fitness_center</i>
      </button>
      <button
        className="btn col l2 m3 s4 offset-l3 offset-m2 offset-s1 waves-effect waves-light"
        onClick={handleSubmit}>
          Submit
          <i className="material-icons right">chevron_right</i>
      </button>
    </div>
  );
};