interface ChipsProps {
  dictionary: string[],
  handleClick: (word: string) => void
}

export default function Chips({ dictionary, handleClick }: ChipsProps) {

  return (
    <div style={{ margin: ".5rem 0" }}>
      {Object.keys(dictionary).sort(
        (a, b) => a.toLowerCase().localeCompare(b.toLowerCase())
      ).map((word, key) => {
        return (
          <a
            className="chip" href="!#" key={key}
            onClick={(e) => {
              e.preventDefault();
              handleClick(word);
            }}>
              {word}
          </a>
        );
      })}
    </div>
  );
};